.card1 {
    border: 3px solid white;
    margin: 15px;
    padding: 30px 45px;
    border-radius: 5px;
    background-color: linear-gradient(to right top, #d16ba5, #c777b9, #ba83ca, #aa8fd8, #9a9ae1, #8aa7ec, #79b3f4, #69bff8, #52cffe, #41dfff, #46eefa, #5ffbf1);
}

.text {
    margin: 5px;
    font-size: x-large;
    font-weight: bolder;
}

.card {
    width: 300px;
    height: 360px;
    background-color: #030029;
    position: relative;
    border-radius: 8px;
    display: grid;
    place-items: center;
    overflow: hidden;
    cursor: pointer;
    margin-top: 30px;
    box-shadow: inset 0 -3em 3em rgba(0, 0, 0, 0.1), 0 0 0 2px #030029,
        0.3em 0.3em 1em rgba(0, 0, 0, 0.3);
}

.card h2 {
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-size: 2rem;
    /* color: #ffa3fd; */
    color: rgb(190 203 228);
    color: rgb(212 222 241);

    /* background: hsla(24, 100%, 83%, 1);

background: linear-gradient(90deg, hsla(24, 100%, 83%, 1) 0%, hsla(347, 99%, 41%, 1) 0%, hsla(345, 18%, 60%, 1) 58%, hsla(344, 32%, 71%, 1) 83%, hsla(341, 37%, 81%, 1) 100%);

background: -moz-linear-gradient(90deg, hsla(24, 100%, 83%, 1) 0%, hsla(347, 99%, 41%, 1) 0%, hsla(345, 18%, 60%, 1) 58%, hsla(344, 32%, 71%, 1) 83%, hsla(341, 37%, 81%, 1) 100%);

background: -webkit-linear-gradient(90deg, hsla(24, 100%, 83%, 1) 0%, hsla(347, 99%, 41%, 1) 0%, hsla(345, 18%, 60%, 1) 58%, hsla(344, 32%, 71%, 1) 83%, hsla(341, 37%, 81%, 1) 100%);
*/
    filter: progid: DXImageTransform.Microsoft.gradient(startColorstr="#FFCAA6", endColorstr="#D0012E", GradientType=1);
    text-transform: uppercase;
    text-align: center;
    z-index: 2;
}

.card::before {
    content: "";
    position: absolute;
    width: 150px;
    height: 140%;
    background-image: linear-gradient(180deg,
            #865fff,
            #e384ff);
    animation: rotate 5s linear infinite;
}

.card::after {
    content: "";
    position: absolute;
    inset: 6px;
    /* background-color: #030029; */
    background: linear-gradient(45deg, rgba(112, 168, 209, 1) 0%, rgba(67, 132, 189, 1) 11%, rgba(33, 66, 133, 1) 39%, rgba(12, 41, 98, 1) 61%, rgba(1, 14, 40, 1) 92%);
    border-radius: inherit;
}

@keyframes rotate {
    100% {
        rotate: 360deg;
    }
}

.card1::before {
    content: "";
    position: absolute;
    width: 150px;
    height: 140%;
    background-image: linear-gradient(180deg,
            #865fff,
            #e384ff);
    animation: rotate 3s linear infinite;
}

.card1::after {
    content: "";
    position: absolute;
    inset: 8px;
    background-color: #030029;
    border-radius: inherit;
}

@keyframes rotate {
    100% {
        rotate: 360deg;
    }
}


@media only screen and (max-width: 948px) {
    .body {
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        align-items: center;
    }
}

.underline {
    border-bottom: 2px solid;
    margin-bottom: 2px;
}